.customize-control-wecodeart-divider {
	&__wrapper {
		margin: 0 -12px;
        padding: 14px 12px;
        border-width: 0 0 0 5px;
		border-style: solid;
		border-color: #2388ed;
		background-color: #fff;
    }

	.customize-control-title {
		display: block;
		margin: 0;
		font-size: 12px;
		font-weight: 600;
		letter-spacing: 0.7px;
		line-height: 1;
		text-transform: uppercase;
    }
    
	.customize-control-caption {
		top: 50%;
		transform: translateY(-50%);
		position: absolute;
		right: 0;
		font-size: 11px;
		font-weight: 500;
		letter-spacing: 1px;
    }
    
	.customize-control-description {
        margin-top: 10px;
	}
}